body{
  font-family: 'Open Sans', sans-serif;
  color: #232323;
}
*, *::before, *::after{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.section-heading{
  font-size: 2rem;
    font-weight: 600;
    margin-bottom: 4rem;
    color: #212121
}
/* main{
  min-height: 80vh;
} */
a:hover{
  text-decoration: none;
}
nav{
  text-align:center;
  position: fixed;
  width:100%;
  z-index:999;
  color: #eee;
}
nav a{
  color: inherit;
}
nav a:hover{
  color: inherit;
}
nav .logo{

}
nav .collapsable{
  position: absolute;
  text-align: left;
  top:100%;
  left:0;
  width:100%;
  -webkit-transform: scale(1,0);
          transform: scale(1,0);
  -webkit-transform-origin: top;
          transform-origin: top;
  transition: -webkit-transform 400ms ease-in-out;
  transition: transform 400ms ease-in-out;
  transition: transform 400ms ease-in-out, -webkit-transform 400ms ease-in-out;
 
}

nav .collapsable ul{
  margin:0;
  padding: 0;
  list-style:none;
}

nav .collapsable li{
  margin-bottom: 1em;
  margin-left: 1em;
}

nav .collapsable a{
  text-decoration: none;
  opacity: 0;
  transition: opacity 200ms ease-in-out;
  transition: color 0.4s ease-in-out;
}

nav .active{
  color: #7cb342;
}
nav .btn-toggle{
  position: absolute;
  top:0;
  left: 1rem;
  height: 100%;
  display: flex;
  align-items: center;
  color: #fff;
  background: transparent;
  border: none;
  font-size: 25px
  
}
nav .btn-toggle svg{
  fill: #666;
}
nav .btn-toggle:focus{
  outline: none;
}
nav .open{
  -webkit-transform: scale(1,1);
          transform: scale(1,1);
}
nav .open a{
  opacity: 1;
  transition: opacity 250ms ease-in-out 250ms;
}
nav .brand{
  color: #eee;
  font-family: 'Berkshire Swash', cursive;
  font-size: 2rem;
}
nav .brand span{
  color: #7cb342;
  font-size: 1.5rem;
}

@media screen and (min-width:800px){
  nav .btn-toggle{
    display:none !important;
  }
  nav{
    display: grid;
    grid-template-columns: 1fr auto minmax(600px, 3fr) 1fr;
  }
  nav .logo{
    grid-column: 2/3;
  }
  nav .collapsable{
    all: unset;
    grid-column: 3 /4;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  nav .collapsable a{
    opacity: 1;
  }
  nav .collapsable a:hover{
    opacity: 0.6;
  }
  nav .collapsable ul{
    display: flex;
  }
  nav .collapsable li{
    margin-bottom: 0;
  }
}




header{
  background:linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url('/images/bg3.jpg')center/cover fixed no-repeat;
  height: 100vh;
}
header .hero-box{
  height: 100vh;
}
header .greeting{
  color: #fff;
  font-size: 3rem;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 1rem;
}
header .profession{
  color: #eee;
font-size: 1.2rem;
font-weight: 500;
line-height: 1.2;
margin-bottom: 20px;
}

header .icons{
  margin-top: 0.5rem;
}
header .icons a{
  color:#fff;
  display: inline-block;
  width: 2.5rem;
  height: 2.5rem;
  margin-left:1rem;
  padding-top: 0.3rem;
  border-radius: 50%;
  background: rgba(117,117,117,.1);
  border: 1px solid rgba(117,117,117,.2);
}
header .icons a{
  fill: #eee;
  transition: fill 0.4s ease;
}
header .icons a:hover{
  fill: #7cb342;
}
header svg{
  width: 1rem;
  height: 1rem;
}
.skills{
  padding: 10rem 0;
}
.skills .skills-icon{
  opacity: 0.6;
}
.portfolio .item{
  position: relative;
  overflow: hidden;
  background: rgb(2, 92, 22);
}
.portfolio .item-image{
  transition: all 0.35s ease;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.portfolio .item:hover .item-image{
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 0.5;
}
.portfolio .item-details{
  position: absolute;
  top: 50%;
  left:50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 1;
}
.portfolio .item-details a{
  margin-left: 1rem;
}
.portfolio .item-details h3{
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
  color: #fff;
  transition: -webkit-transform 0.35s;
  transition: transform 0.35s;
  transition: transform 0.35s, -webkit-transform 0.35s;
}

.portfolio .item:hover .item-details h3{
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.portfolio .icons{
  transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
  transition: opacity 0.35s, transform 0.35s, -webkit-transform 0.35s;
  -webkit-transform: translateY(20px);
          transform: translateY(20px);
  opacity: 0;
}
.portfolio .item:hover .item-details .icons{
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
}
.portfolio a{
  fill: #eee;
  transition: fill 0.4s ease;
}
.portfolio a:hover{
  fill: #7cb342;
}

.portfolio .item::before, .portfolio .item::after{
  content: '';
  position: absolute;
  top:1.2rem;
  bottom: 1.2rem;
  left: 1.2rem;
  right: 1.2rem;
  transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
  transition: opacity 0.35s, transform 0.35s, -webkit-transform 0.35s;
  opacity: 0;
  background: none;
}
.portfolio .item::before{
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
  -webkit-transform: scale(0,1);
          transform: scale(0,1);
}
.portfolio .item::after{
  border-left: 1px solid #fff;
  border-right: 1px solid #fff;
  -webkit-transform: scale(1,0);
          transform: scale(1,0);
}
.item-container:hover .item::before,
.item-container:hover .item::after
{
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
}
#portfolio{
  background:linear-gradient(rgba(255,255,255,0.4), rgba(255,255,255,0.4)), url('/images/hero.jpg')center/cover fixed no-repeat;
